.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /* background-color: rgba(0, 0, 0, 0.25); */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: auto;
    z-index: 9999;
  }
  
  .overlay-content {
    background: white;
    border: 1px solid black;
    padding: 20px;
    max-width: 80%;
    max-height: 80%;
    overflow: auto;
    position: relative;
  }
  
.overlay-content img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    margin: auto;
    display: block;
  }
  
.overlay-close-button {
    position: absolute; /* changed from relative to absolute */
    top: 10px; /* adjust as needed */
    right: 10px; /* adjust as needed */
    z-index: 100; /* ensures the button is above other content */
  }

.overlay-content-wrapper {
    display: flex;
    width: 100%;
    height: 100%;
}

.overlay-text {
    width: 30%; 
    padding: 0 10px;
    margin-right: 30px; 
}

.overlay-image {
    width: 70%;
    padding: 10px;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.overlay-image img {
    padding-bottom: 10px;
}

.overlay-tag {
    font-size: 1rem;
    padding: 3px 6px;
    margin-right: 6px;
    border-radius: 5px;
}

.overlay-close-button {
    border-radius: 50%;
    border: 0.5px solid black;
}